<template>
  <div class="skill">
    <h1 class="main-title animated bounceInRight">Technology stack</h1>
    <div class="content flex-wrap jus-around">
      <div class="box odd">
        <div class="item item1" data-tilt data-tilt-scale="1.1">
          <h1>01</h1>
          <ol>
            <li> HTML,DOM</li>
            <li> CSS BFC</li>
            <li> flex </li>
          </ol>
        </div>
      </div>
      <div class="box even">
        <div class="item item2" data-tilt data-tilt-scale="1.1">
          <h1>02</h1>
          <ol>
            <li>1.  Javascript  es6</li>
            <li>2. this</li>
            <li>3.  webajaxpromiseawait/async</li>
            <li>4. </li>
          </ol>
        </div>
      </div>
      <div class="box odd">
        <div class="item item3" data-tilt data-tilt-scale="1.1">
          <h1>03</h1>
          <ol>
            <li> jquerybootstrap</li>
            <li> sass mixin</li>
            <li> jsonp</li>
          </ol>
        </div>
      </div>
      <div class="box even">
        <div class="item item4" data-tilt data-tilt-scale="1.1">
          <h1>04</h1>
          <ol>
            <li> vueJs </li>
            <li> vue-router vuex </li>
            <li> MVC MVVM </li>
          </ol>
        </div>
      </div>
      <div class="box odd">
        <div class="item item5" data-tilt data-tilt-scale="1.1">
          <h1>05</h1>
          <ol>
            <li> http </li>
            <li> gulpwebpack parcel</li>
            <li></li>
            <li></li>
          </ol>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import VanillaTilt from "vanilla-tilt";
import $ from "jquery";
export default {
  mounted() {
    if ($(window).width() > 500) {
      this.initVanilaTilt();
      this.toggleClass(1);
    } else {
      this.init();
      this.initScroll();
    }
  },
  destroyed() {
    $(".container").animate({ scrollTop: 0 }, 500);
  },
  methods: {
    init() {
      setTimeout(() => {
        $(".skill .box")
          .first()
          .addClass("scale");
      }, 50);
    },
    initScroll() {
      $("#app .container").on("scroll", function(e) {
        $(".skill .box").each((i, el) => {
          if (
            $(el).offset().top <= 259 &&
            $(el).offset().top + $(el).height() <= 553
          ) {
            $(el)
              .addClass("scale")
              .siblings()
              .removeClass("scale");
          }
        });
      });
    },
    toggleClass(n) {
      if (n === 1) {
        $(".skill .box.even").addClass("animated bounceInUp");
        $(".skill .box.odd").addClass("animated bounceInDown");
      }
    },
    initVanilaTilt() {
      VanillaTilt.init(document.querySelector(".content .item1"), {
        max: 25,
        speed: 400
      });
      VanillaTilt.init(document.querySelector(".content .item2"), {
        max: 25,
        speed: 400
      });
      VanillaTilt.init(document.querySelector(".content .item3"), {
        max: 25,
        speed: 400
      });
      VanillaTilt.init(document.querySelector(".content .item4"), {
        max: 25,
        speed: 400
      });
      VanillaTilt.init(document.querySelector(".content .item5"), {
        max: 25,
        speed: 400
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.skill {
  color: #fff;
  padding: 50px 40px;
}
.content {
  margin-top: 90px;
  .box {
    .item {
      width: 190px;
      padding: 10px;
      font-size: 14px;
      margin-bottom: 20px;
      min-height: 275px;
    }
    &:nth-child(even) .item {
      background: rgba(5, 146, 92, 0.986);
    }
    &:nth-child(odd) .item {
      background: rgba(255, 255, 255, 0.3);
    }
    li {
      margin: 15px 0;
    }
    h1 {
      color: rgba(255, 255, 255, 0.3);
    }
  }
}
@media screen and (max-width: 500px) {
  .skill .content {
    margin: 58px;
    .box {
      transform: scale(1);
      transition: transform 0.5s;
      &.scale {
        transform: scale(1.2);
      }
    }
  }
}
</style>


